<template>
  <div class="aboutus inner mb20">
    <el-row>
      <el-col :xs="24" :sm="10" :md='10' class="pa10 textAlignLeft">
        <h4 class="mt20">“让手机简单好用！”</h4>
        <p>——答“诠星游戏平台究竟是做什么的”</p>
        <p>“诠星”们的梦想，是用炫酷的技术和精良的设计，让每个人都能更简单地把应用，音乐等等好东西放进他的手机。让我们的努力“面向大众、惠及人人”。</p>
        <p>“从解决问题，到更好地解决问题”，是诠星游戏思考与行动的起点，也是诠星游戏的核心竞争力。面对一个平坦、开放的世界，唯有专注于始终为用户解决问题，才能持续突破。</p>
        <p>快速成长的诠星游戏实验室，在“成年”后，该是家什么气质的企业？我们的答案是——真心维护用户利益、真心关注员工个人成长。我们的团队，要有核心价值观，也要保持理想、保持激情。</p>
        <p>关于我们</p>
        <p class="font12">诠星游戏实验室隶属于北京卓易讯畅科技有限公司，成立于2010年9月，旗下产品包括诠星游戏、诠星游戏应用推广、诠星游戏一键安装等。</p>
        <p>联系地址：</p>
        <p>联系我们</p>
